const Paragraph1 = 
<div className="content">
  <h1 className="content">[] ()</h1>
  <p className="content">
Xeno-polska nadchodzi z przyszoci. Wszystkie potgi i siy ujemnego sprzenia zwrotnego poczyy si w (re)produkcji przewidywalnych skutkw i konsekwencji: obodziski i Dymek, Opinia Bieca i Wysokie Obcasy, Tygodnik Powszechny i Dwutygodnik oraz tysice botw z jedyn funkcj crtl+c ctrl+v  - wszyscy oni, agenci i agencje stabilizujce zaistniae zdarzenie w retoryce protokow rozpisanych reakcyjnymi siatkami domykajcymi nowotworzce 0 w ramach ekonomii dwjkowej wymiany.
  </p>
  <p className="content">
Prawicowy dziaacz kpi z molestowanej feministki: Czy ta Pani sama siebie nie traktuje przedmiotowo?". Media zwlekaj z zajciem stanowiska do czasu ogoszenia wyroku  ryzyko, jakie niesie wykroczenie z konformistycznego sceptycyzmu, zagraa ich fantazji o pozycji bezstronnego opiniotwrcy. Dwojaki wniosek wypywa z tego faktu. To, co wyalienowane, jest ju przez wszystkie siy uznane za potg. Czas ju najwyszy, abymy, my z prefiksem xeno-, wyoyy otwarcie wobec caego wiata obcy punkt widzenia, odlege cele, nieznane denia i bajce o zym dysekwilibrium przeciwstawili manifest 0-dodatniej seksualnoci dla xeno-polski. Napisaymy go z przyszoci i przeszoci, nierwnomiernie dystrybuowanych poprzez ukady, figury, materiay i wyobraenia, ktrych wielo skada si na kady wiat. Uylimy wszystkiego, co byo udostpnione, cignite, spiracone. Tego, co najblisze, i tego, co najdalsze. Zebraymy si z najrniejszych zaktkw kompleksu czasoprzestrzennego, by wyonia si alienomantyczna technopleksja anty-patriarchatu, oznaczona tagami: akceleracja roztopu, cienie 3D proxyself, psot-klubowa memetyczna amplifikacja, zombie triy, propagacja hiperstycyjnych nonikw, ekrany AI-lubrykacji, blockchain libidinalny, enkrypcyjny kamufla, ambientalny biohacking, voodoo antyhumanizm, komputozofia, syntetyczny feminizm, kognitywny freeganizm, XF-szczepienie, dyssypacyjna proliferacja, crustacean love, lesbijski wampiryzm, spirotemporalny demonizm, bakteryjny khattak, efektywny neo-nihilizm, transbionika, inteligentny wetware, zakaenie hy45r, wirusowa amnezja, cyberinsurekcja, schizotaktyka, pulpomutacja, neotropia, elo wale wiadro.
  </p>
  <p className="content">
Nie potrzebujemy nie-przedmiotw, nie-wyalienowanych caoci wpisanych w naturalne procesy wymiany i reprodukcji. Jestemy wyalienowane i to kochamy - to, czego potrzebujemy i czego damy, to wyjcie z bagna bezporednioci skazujcego nas na pust lubrykacj "naturalnego" porzdku wymiany ekonomiczno-afektywnej, w ktrym zajmujemy dziurawe miejsce mwicych, lecz w ostatniej instancji niemych ofiar. Nie tyle pragniemy rozpuci si w roztworze jednorodnej zasady, by nigdy wicej ju nie wypowiedzie ja, ile zanurzy w plastycznolepkich pynach, w ktrych przestaje mie znaczenie, co i czy mwi ja. Kade pozna swoje. Nie jestemy ju sob.
  </p>  
  <p className="content">
To, co z jednej perspektywy stao si katastrof, z innej wkracza jako pozytywna przyszo, nieuchronna jak kropla paku wglowego, ktrej prdko opadania zaley od temperatury ukadu. Postulujemy cyberdodatnio  to znaczy: nieprzewidziane linie wyjcia poza cyberujemne struktury kontroli i rwnowagi, otwarcie kodu rdowego na wielo Zewntrznych zmian i interwencji.
  </p>  
</div>

const Paragraph2 = 
<div className="content">
  <h1 className="content">=( )))</h1>
  <p className="content">
Beznadziejnie pospolite wydarzenie wycieko spod zamknitych drzwi, eskaluje po mediach spoecznociowych, zalewajc wasze dotykowe, nasycone i ywe ekrany. Przemoc seksualna roi si w sypialniach, w neuronach, w algorytmach, na ulicach, wszyscy o tym wiedz. Czujnik owietlenia sam dostosowuje jasno ekranu do warunkw zewntrznych, a powoka antyrefleksyjna dodatkowo go zabezpiecza i zapewnia najlepsz widoczno w warunkach natenia wiata. Ogldamy dzie zerowy, syszysz, jak wypowiedzielimy wojn tej samej, smutnej historii opowiadanej od zbyt dawna.
  </p>
  <p className="content">
Patriarchat nie sprowadza si do patologicznych zachowa jednostek. Dymek-oprawca" i Dymiska-ofiara" wprowadz nas w dzie wistaka redukowania polityki do dramatu osobistych namitnoci. Patriarchat to wszechogarniajca machina petryfikacji rzeczywistoci w obwodach powracajcych agresji, przemocy, gwatu. W obieniach jego matrycy snuj si wyssane z mocy i wyczerpane awatary, ktre czy natenie bezsilnoci na osi dynamiki wiskotycznej. Martwe tkanki patriarchatu odkadaj si na adresach ip, adresach pocztowych, adresach url, adresach fizycznych, adresach publicznych. Do kadego trzeba innych taktyk i technik przystosowania rodowiska, by mogo nareszcie po raz pierwszy w tym eonie przej w faz hipertrofii. Przeczymy si do innego wiata tylko dziaajc wsplnie. Bdmy web crawlerami indeksujcymi resztki z fantazji o naturalnym, bezporednim wiecie, rozpisanym na binaryzm kobieta/mczyzna. Twrzmy warunki cieplarniane, zmieniajmy toksyczn oikonomi, stawajmy si koprofagami i nekrofagami. Jeli system prawny uywa nekrofauny w kryminalistyce  my skadajmy jaja w wieym misie, jeszcze zanim zostanie poddane obrbce estetycznej i sprzedane na kilogramy. Powicamy nasze larwy w rytuale doprowadzenia gnijcej kultury gwatu do cakowitego rozkadu. Pomylmy o tych wszystkich mineraach, jakie przedostan si do ziemi po dezintegracji.
  </p>  
</div>

