const Paragraph7 = 
<div className="content">
  <h1 className="content"> :    : </h1>
  <p className="content">
Nie mwimy w imieniu Innego, Innego-ucinionego, Innego w niszej pozycji w relacjach wadzy. Dyskurs wspczucia zastawia puapki frazesw i protekcjonalnoci na humanist, ktry chciaby podda krytyce relacje wadzy za pomoc najnowszego aparatu pojciowego, wypracowanego w edgy-laboratoriach idei i wyjani Innemu jego wiat. Nie fingujmy usytuowania, z ktrego nie bdziemy w stanie si wypowiada - wolimy wyspekulowywa linie emancypacji z patriarchatu. Sigajmy po wyzyskiwane, zawaszczane, eksploatowane materiay i kultury, lecz nie z racji ich byciarepresjonowanymi  sam fakt znajdowania si niej w relacjach wadzy nie jest godny afirmacji. Formuj one molekularne roje, sprytne wirusy cyfrowe, z ktrych interesuj nas sposoby ekspansji, infekcji, propagacji, replikacji przyszoci. Gestem pochylenia si nad grup ucinion pozbawia si jej jakiejkolwiek mocy, zdolnoci do przechytrzenia hegemona, zakaania tego, co opresyjne, wykorzystywania istniejcej materialnej maszynerii (re)produkcji, dystrybucji, konsumpcji norm oraz regulacji ycia i mierci. Podporzdkowani yj w tym samym rodowisku technologicznym, co ci posiadajcy kapita kulturowy czy finansowy, maj dostp do tych samych programw, nakadek i mediw i wiedz, jak ich uy. W cieniu postulatu emancypacji kryje si protekcjonalno. Nie pragniemy stworzy wsplnego jzyka dla rnych klas, ras i pci, ale nakreli linie spjnego wiata, z ktrego kade bdzie czerpa wicej mocy.
  </p>
  <p className="content">
Z pewnoci na jakim poziomie istnieje napicie midzy libertarianizm bogatych a konserwatyzmem biednych - czy jednak wiek ich analizowania, kategoryzowania, przeciwstawiania i pojednywania nie przemin ju niemal dwie dekady temu? Wyalienowanie, koniecznie powizane z seksualnoci, zbiega si z syntetycznymi liniami wyjcia z kondycji, ktra pozbawia mocy, bez wzgldu na jej pooenie w klasycznie rozumianych stosunkach wadzy. Potraktujmy powanie technologi, nie dlatego by znie o relacji wadzy: rasy, klasy, pci, lecz aby stworzy nowe sposoby upodmiotowienia, indywiduacji, uwsplnotowienia. Walka z wyzyskiem klasowym nie wymaga zastosowania narzdzi z dyskursu klasowego i uwikania si we wsplny interes klasowy  w zamian oddajmy si sprzeniu kapitau kulturowego i ukadu technologicznego, ktrego ucieczkowe trajektorie wykraczaj daleko poza intencje poszczeglnych grup spoecznych. Przyjmujmy taktyki o bardziej globalnych orbitach, ni korzy lokalnej grupy interesu (czy mona nadal wierzy, e interes klasowy jest w stanie pokona choby bariery pastwowo-narodowe?).
  </p>
  <p className="content">
Doszlimy do zapory sieciowej. Pojcia, w jakich problematyzowao si te wielkie bloki: pe, klasa, rasa wyobiy gbokie i mocne koryta, ktrymi pyn opady kognitywne. Wszystkie strony sporu przechodz przez znane cigi argumentacyjne i reakcje, kady ma poczucie repetycji, co ostatecznie sprowadza si do wyprodukowania nastpnej generacji symptomw wzmacniajcych t sam katastrofaln neuroz. Pojcia te osigny pewn granic funkcjonalnoci i dzi dziaaj wycznie na rzecz reprodukcji marazmu. Nie zdoaj ju wzmacnia eksperymentalne formuowanie problemw, dla ktrych rozwizania nie byy nieskoczokrotnie powtarzane na ulicach, uniwersytetach, mediach spoecznociowych, w czasopismach, w telewizji, podczas konferencji i kampanii wyborczych. Sformuowanie problemu seksualnoci w kategoriach liberalni/bogaci/biedni/konserwatywni, stwierdzenie, e jedni to s/o_prawcy, drugie - bierne ofiary (systemu, przemocy indywidualnej, warunkw ekonomicznych, klimatycznych, rodzinnych) pozbawia wszystkie strony sprawczoci w przywoywaniu czego innego, moliwoci sigania po nowe. Nikt nie chce by nazywany ofiar. Paradygmat podziau na role ofiary i oprawcy prowadzi do sytuacji bez wyjcia. Stawk jest pojcie podmiotowoci, ktre musi obejmowa jednoczenie twoje dowiadczenia zwizane z przemoc oraz sprawczo, ktr masz i ktra doprowadzia ci do sytuacji, w ktrej aktualny dyskurs nadaje ci klaustrofobiczny, duszcy predykat: ofiara. Paradygmat, ktry wymusza wchodzenie w opresyjne sytuacje majc wiedz o ich przemocowym charakterze, bo to jedyna forma upodmiotowienia, albo jedna z kilku podobnych, z ktrych reszta jest tak zupenie nieakceptowalna, bo oferuj jeszcze aoniejsze poczucie braku mocy. Nazywanie kogo oprawc rwnie nie przynosi poytku, uruchamia procedury koza ofiarnego, ktre w ceremonialnym spaleniu za ucielenionego oczyszczaj spoeczno jedynie na poziomie fantazmatycznym, zachowujc wszystkie warunki produkcji przemocy seksualnej. A do nastpnego razu. To problem obrazw, ktre powtarzaj si od tak dawna w naszej kulturze, tych samych opowieci wplecionych w banie, w mity, w praktyki seksualne, w wyrabianie codziennoci.
  </p>
  <p className="content">
Nie mona porzuca trzech klasycznych osi - rasy, klasy i pci - oraz dziedzictwa kulturowego, historycznego, politycznego, rewolucyjnego, jakie wok nich si wytworzyo  naley jednak pomyle je geologicznie. Urodziam si i stawaam w specyficznym usytuowaniu - co mog  z tym zrobi? W jakie afekty mog wej, wmontowa si i queerowa? Czy rotacja cyberdodatniej seksualnoci nie przebiega na wskro tamtych podziaw? Prbujemy pomyle ukone upodmiotownienia, ktre stan si kopotem dla hegemonicznego systemu wiedzy, nawykw i wadzy.
  </p>
</div>

